.projectscontainer { 
    background-color: #1D1B26; 
    height:58rem; 
    width:100%; 
    display:flex; 
    flex-direction:row; 
    flex-wrap: wrap; 
    justify-content: left; 
    grid-column:1; 
    grid-row: 2;

}
.entireprojectscontainer { 
    width: 100%; 
    background-color: #1D1B26; 
    color: white; 
    display:grid; 
    grid-template-columns: 100%; 
    grid-template-rows: auto auto; 
    
    
}
.text45 { 
    margin-left:48%;
    font-size:2rem;
    margin-top:2%; 
    width:100%;
    grid-column: 1; 
    grid-row: 1; 
}

